.calculator-body {
  height: 340px;
  width: 240px;
  background-color: aliceblue;
  opacity: 0.95;
  z-index: 15;
  border-radius: 7px;
  box-shadow: 0.1px 0.5px hsl(0, 0%, 60%), inset 0.1px 0px gray;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#calculation {
  font-size: 20px;
  width: 95%;
  margin: 2px;
  height: 27%;
  padding: 6px;
  resize: none;
  border-radius: 8px;
  border: 0.2px solid #d7ebfd;
  outline: none;
  font-size: 15px;
}
.calc-btns {
  flex: 1;
  width: 95%;
  margin: 4px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.clac-btn {
  height: 45px;
  width: 45px;
  margin: 5px;
  border-radius: 5px;
  background-color: #cee5f8;
  font-size: 19px;
}
